<script setup lang="ts">
import { defineProps, withDefaults } from "vue";
import { prop } from "vue-class-component";
import type { VCard } from "vuetify/components";
import ButtonComponent from "./ButtonComponent.vue";
type TVariant = VCard["$props"]["variant"];
type TDensity = VCard["$props"]["density"];

interface Props {
  variant?: TVariant;
  density?: TDensity;
  height?: string | number;
  width?: string | number;
  color?: string;
  class?: string;
  text?:
    | {
        name: string;
        syozoku: string;
        kana: string;
        myimage_path: "";
        company_name: "";
        company_image_path: "";
        company_url: "";
        company_address: [{ key: 0; value: ""; map_url: "" }];
        tel: "";
        email: "";
        skills: [{ key: 0; value: "" }];
        histories: [{ key: 0; title: ""; value: "" }];
        profile: "";
      }
    | any;
}

const props = withDefaults(defineProps<Props>(), {
  variant: "outlined",
  density: undefined,
  height: undefined,
  width: undefined,
  color: undefined,
  class: undefined,
});
</script>
<template>
  <v-card
    :variant="props.variant"
    :density="props.density"
    :height="props.height"
    :width="props.width"
    :color="props.color"
    :class="props.class"
  >
    <template v-slot:text>
      <b></b><br />
      <b
        ><br />
        </b
      >
      <p class="text-caption"></p>
      <p>{{ props.text?.name }}</p>

      <p class="text-caption"></p>
      <p>{{ props.text?.email }}</p>
      <p class="text-caption"></p>
      <p>{{ props.text?.tel }}</p>
      <p class="text-caption"></p>
      <p> {{ props.text?.postcode }}</p>
      <p>{{ props.text?.pref }}</p>
      <p>{{ props.text?.address }}</p>

      <div class="mt-5">
        <ButtonComponent
          label=""
          @onClick="$emit('inputBack')"
        ></ButtonComponent>
        <ButtonComponent
          label=""
          color="primary"
          class="ml-2"
          @onClick="$emit('inputRegist')"
        ></ButtonComponent>
      </div>
    </template>
  </v-card>
</template>
